body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: pink;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header {
  background-color: white;
  display: flex;
  justify-content: space-between;
  height: 3em;
  align-items: center;
}

.nav {
  position: relative;
  margin-right: 1em;

  ul {
    display: flex;
    list-style: none;
    gap: 2em;
    padding: 0;
    margin: 0;
  }

  a {
    color: black;
    text-decoration: none;
    font-size: large;
  }
}

.logo {
  margin-left: 1em;
}

.mobileMenuButton {
  display: none;
}
.img-splash{
  height: 50em;
  background-image: url("./images/1.jpg");
  background-size: cover;
  background-position: center;
}

.menu-items-wrapper {
  background-image: url("./images/dddepth-319.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
}

.menu-title {
  margin: 0;
  padding: 1em;
  text-align: center;
}

.itemlist-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.card-container {
  background-color: rgba(0, 13, 161, 0.365);
  backdrop-filter: blur(3em);
  margin: 2em;
  width: min-content;

  img {
    height: 20em;
  }
}
.item-name {
  color: rgb(255, 191, 0);
  margin-left: 1rem;
}

.item-price {
  font-size: 2.5em;
  color: white;
  margin-right: 1rem;
}

.description {
  display: none;

  p {
    color: white;
    margin: 0;
    padding: 1em;
  }
}

.description.expanded {
  display: block;
}

.more-button {
  margin-bottom: 1em;
}

.row {
  display: flex;
  flex-direction: row;
  align-items: baseline;
  justify-content: space-between;
}

.solo-row {
  display: flex;
  justify-content: center;
}

footer {
  background-color: cadetblue;
  padding: 20px;
  display: grid;
  justify-content: center;
}

footer p {
  font-size: 14px;
  color: #ffffff;
  margin: 0;
}

footer p:first-child {
  margin-bottom: 10px;
}

@media (min-width: 1140px) {
  .splash-container {
    max-height: 80%;
    overflow: hidden;
  }
}


@media (max-width: 768px) {
  .splash-container{
    height: auto;
  }
  html{
    font-size: 12px;
  }
  .mobileMenuButton {
    display: block;
    margin-right: 1em;
  }

  .nav {
    display: none;
  }

  .nav.expanded {
    display: block;
    position: absolute;
    top: 3em;
    width: 100%;

    ul {

      display: grid;
      background-color: white;
    }

    a {
      display: block;
      width: 100%;
      text-align: center;
      text-decoration: none;
      font-size: large;
      padding: 2em 0;
    }

  }
}